import { useState } from 'react';

export default function CodeEditor({ code, setCode, disabled, onRun, onDownload, loading, locked }) {
  const [showWarning, setShowWarning] = useState(false);

  const showToast = (message) => {
    setShowWarning(message);
    setTimeout(() => setShowWarning(false), 3000);
  };

  const handleEditorChange = (value) => {
    setCode(value || '');
  };

  return (
    <div className="relative rounded-xl overflow-hidden bg-white dark:bg-gray-900 border border-gray-200 dark:border-gray-700 shadow-lg">
      {/* Header */}
      <div className="flex items-center justify-between px-6 py-4 bg-gray-50 dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700">
        <div className="flex items-center gap-3">
          <div className="flex gap-2">
            <div className="w-3 h-3 rounded-full bg-red-500"></div>
            <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
            <div className="w-3 h-3 rounded-full bg-green-500"></div>
          </div>
          <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">Python Editor</span>
        </div>
        
        <div className="flex items-center gap-3">
          {loading && (
            <div className="flex items-center gap-2 px-3 py-2 rounded-lg bg-blue-50 dark:bg-blue-900/30 border border-blue-200 dark:border-blue-700">
              <div className="w-3 h-3 rounded-full bg-blue-500 animate-pulse"></div>
              <span className="text-sm text-blue-700 dark:text-blue-300 font-medium">Running...</span>
            </div>
          )}
          
          <button
            onClick={onRun}
            disabled={locked || loading}
            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 flex items-center gap-2 ${
              locked || loading
                ? "bg-gray-100 dark:bg-gray-700 text-gray-400 cursor-not-allowed"
                : "bg-green-500 hover:bg-green-600 text-white shadow-md hover:shadow-lg"
            }`}
          >
            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM9.555 7.168A1 1 0 008 8v4a1 1 0 001.555.832l3-2a1 1 0 000-1.664l-3-2z" clipRule="evenodd" />
            </svg>
            Run Code
          </button>
          
          <button
            onClick={onDownload}
            disabled={locked || loading}
            className={`px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 flex items-center gap-2 ${
              locked || loading
                ? "bg-gray-100 dark:bg-gray-700 text-gray-400 cursor-not-allowed"
                : "bg-blue-500 hover:bg-blue-600 text-white shadow-md hover:shadow-lg"
            }`}
          >
            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M3 17a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm3.293-7.707a1 1 0 011.414 0L9 10.586V3a1 1 0 112 0v7.586l1.293-1.293a1 1 0 111.414 1.414l-3 3a1 1 0 01-1.414 0l-3-3a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
            Export PDF
          </button>
        </div>
      </div>

      {/* Editor Area */}
      <div className="relative bg-gray-50 dark:bg-gray-800">
        <textarea
          value={code}
          onChange={(e) => handleEditorChange(e.target.value)}
          disabled={disabled}
          placeholder="# Write your Python code here..."
          className="w-full h-96 p-6 pl-12 bg-transparent resize-none outline-none font-mono text-sm text-gray-800 dark:text-gray-200 leading-relaxed"
          style={{ fontFamily: "'JetBrains Mono', 'Courier New', monospace" }}
          onKeyDown={(e) => {
            if ((e.ctrlKey || e.metaKey) && ['v', 'c', 'x'].includes(e.key.toLowerCase())) {
              e.preventDefault();
              showToast("Clipboard actions are disabled!");
            }
          }}
          onContextMenu={(e) => {
            e.preventDefault();
            showToast("Right-click is disabled!");
          }}
        />
        
        {/* Line Numbers */}
        <div className="absolute left-0 top-0 p-6 pr-3 text-xs text-gray-400 dark:text-gray-600 font-mono leading-relaxed pointer-events-none select-none">
          {code.split('\n').map((_, index) => (
            <div key={index} className="h-5">
              {index + 1}
            </div>
          ))}
        </div>
      </div>

      {/* Warning Toast */}
      {showWarning && (
        <div className="absolute top-20 right-4 z-50">
          <div className="bg-amber-500 text-white px-4 py-3 rounded-lg shadow-lg border border-amber-600 flex items-center gap-2 animate-in slide-in-from-top-2 duration-300">
            <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
            </svg>
            <span className="font-medium">{showWarning}</span>
          </div>
        </div>
      )}
    </div>
  );
}