"use client";
import { useState } from "react";
import CodeEditor from "../components/CodeEditor";
import OutputPane from "../components/OutputPane";
import ModeToggle from "../components/ModeToggle";

// Simple Resizable Panel Components (if you don't have shadcn/ui resizable)
function ResizableContainer({ children, className = "" }) {
  return (
    <div className={`flex flex-col h-full ${className}`}>
      {children}
    </div>
  );
}

function ResizablePanel({ children, className = "", defaultSize = 50 }) {
  return (
    <div className={`flex-1 ${className}`} style={{ flexBasis: `${defaultSize}%` }}>
      {children}
    </div>
  );
}

function ResizableHandle({ className = "" }) {
  return (
    <div className={`h-1 bg-gray-300 dark:bg-gray-600 hover:bg-gray-400 dark:hover:bg-gray-500 cursor-row-resize transition-colors ${className}`} />
  );
}

export default function HomeWithResizable() {
  const [code, setCode] = useState("# Write your Python code here\nprint('Hello, World!')\n");
  const [output, setOutput] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  
  // Mock proctoring data
  const [violations] = useState(1);
  const [locked] = useState(false);

  const runCode = async () => {
    setLoading(true);
    setError("");
    setOutput("");
    
    try {
      // Simulate API call
      setTimeout(() => {
        if (code.includes('error')) {
          setError("NameError: name 'error' is not defined");
        } else if (code.includes('print')) {
          setOutput("Hello, World!\nCode executed successfully!");
        } else {
          setOutput("Code executed successfully (no output)");
        }
        setLoading(false);
      }, 2000);
      
    } catch (err) {
      setError("Execution failed. Please try again.");
      setLoading(false);
    }
  };

  const downloadPdf = async () => {
    try {
      alert("PDF download would be triggered here");
    } catch (err) {
      setError("PDF generation failed.");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50 dark:from-gray-900 dark:via-gray-800 dark:to-gray-900 transition-colors duration-300 flex flex-col">
      {/* Header */}
      <header className="bg-white dark:bg-gray-800 border-b border-gray-200 dark:border-gray-700 px-6 py-4 shadow-sm flex-shrink-0">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
              Python Code Editor
            </h1>
            <div className="flex items-center gap-4 mt-2 text-sm">
              <span className="text-gray-600 dark:text-gray-400">
                Violations: <span className={`font-semibold ${violations > 2 ? 'text-red-600 dark:text-red-400' : 'text-orange-600 dark:text-orange-400'}`}>{violations}</span> / 3
              </span>
              {locked && (
                <span className="px-3 py-1 rounded-full bg-red-100 dark:bg-red-900/30 text-red-800 dark:text-red-200 text-xs font-medium border border-red-200 dark:border-red-800">
                  🔒 Session Locked
                </span>
              )}
            </div>
          </div>
          <ModeToggle />
        </div>
      </header>

      {/* Main Content with Resizable Panels */}
      <div className="flex-1 p-6">
        <div className="h-full bg-white dark:bg-gray-800 rounded-xl shadow-lg overflow-hidden">
          <ResizableContainer>
            {/* Code Editor Panel */}
            <ResizablePanel defaultSize={60} className="min-h-0">
              <div className="h-full">
                <CodeEditor 
                  code={code} 
                  setCode={setCode} 
                  disabled={locked}
                  onRun={runCode}
                  onDownload={downloadPdf}
                  loading={loading}
                  locked={locked}
                />
              </div>
            </ResizablePanel>

            <ResizableHandle />

            {/* Output Panel */}
            <ResizablePanel defaultSize={40} className="min-h-0">
              <div className="h-full flex flex-col">
                <div className="px-6 py-4 bg-gray-50 dark:bg-gray-700 border-b border-gray-200 dark:border-gray-600 flex-shrink-0">
                  <h2 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-green-500"></div>
                    Console Output
                  </h2>
                </div>
                <div className="flex-1 p-6 min-h-0">
                  <OutputPane output={output} error={error} />
                </div>
              </div>
            </ResizablePanel>
          </ResizableContainer>
        </div>
      </div>
    </div>
  );
}